<template>
  <div>
    <div class="input-number-range" :class="{ 'is-disabled': disabled }">
      <div class="flex">
        <div class="from">
          <Input
              ref="input_from"
              v-model="userInputForm"
              :disabled="disabled"
              placeholder=""
              @blur="handleBlurFrom"
              @focus="handleFocusFrom"
              @input="handleInputFrom"
              @change="handleInputChangeFrom"
          ></Input>
        </div>
        <div class="center">
          <span></span>
        </div>
        <div class="to">
          <Input
              ref="input_to"
              v-model="userInputTo"
              :disabled="disabled"
              placeholder=""
              @blur="handleBlurTo"
              @focus="handleFocusTo"
              @input="handleInputTo"
              @change="handleInputChangeTo"
          ></Input>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Input } from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

export default {
  name: 'InputNumberRange',
  components: { Input },
  props: {
    // 
    value: { required: true },

    // 
    disabled: {
      type: Boolean,
      default: false
    },

    // 
    precision: {
      type: Number,
      default: 0,
      validator(val) {
        return val >= 0 && val === parseInt(val, 10);
      }
    },

    //name
    name: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      userInputForm: null,
      userInputTo: null
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(value) {
        /**  */
        if (value instanceof Array && this.precision !== undefined) {
          this.userInputForm = typeof value[0] === 'number' ? value[0] : null;
          this.userInputTo = typeof value[1] === 'number' ? value[1] : null;
        }
      }
    }
  },

  methods: {
    // 
    toPrecision(num, precision) {
      if (precision === undefined) precision = 0;
      return parseFloat(
          Math.round(num * Math.pow(10, precision)) / Math.pow(10, precision)
      );
    },

    handleBlurFrom(event) {
      this.$emit('blurfrom', event);
    },

    handleFocusFrom(event) {
      this.$emit('focusfrom', event);
    },

    handleBlurTo(event) {
      this.$emit('blurto', event);
    },

    handleFocusTo(event) {
      this.$emit('focusto', event);
    },

    handleInputFrom(value) {
      this.$emit('inputfrom', value);
    },

    handleInputTo(value) {
      this.$emit('inputto', value);
    },

    // fromchange
    handleInputChangeFrom(value) {
      // null
      if (isNaN(value)) {
        this.$emit('input', [null, this.userInputTo]);
        this.$emit('changefrom', this.userInputForm);
        return;
      }
      // kongnull''
      if (!value) {
        this.$emit('input', [null, this.userInputTo]);
        this.$emit('changefrom', this.userInputForm);
        return;
      }
      // 
      this.userInputForm = this.setPrecisionValue(value);

      // from > to fromto
      if (typeof this.userInputTo === 'number') {
        this.userInputForm =
            parseFloat(this.userInputForm) <= parseFloat(this.userInputTo)
                ? this.userInputForm
                : this.userInputTo;
      }
      this.$emit('input', [this.userInputForm, this.userInputTo]);
      this.$emit('changefrom', this.userInputForm);
    },

    // tochange
    handleInputChangeTo(value) {
      // null
      if (isNaN(value)) {
        this.$emit('input', [this.userInputForm, null]);
        this.$emit('changefrom', this.userInputTo);
        return;
      }
      // kongnull''
      if (!value) {
        console.log('');
        this.$emit('input', [this.userInputTo, null]);
        this.$emit('changefrom', this.userInputForm);
        return;
      }
      // 
      this.userInputTo = this.setPrecisionValue(value);

      // to < from tofrom
      if (typeof this.userInputForm === 'number') {
        this.userInputTo =
            parseFloat(this.userInputTo) >= parseFloat(this.userInputForm)
                ? this.userInputTo
                : this.userInputForm;
      }
      this.$emit('input', [this.userInputForm, this.userInputTo]);
      this.$emit('changeto', this.userInputTo);
    },

    // 
    setPrecisionValue(value) {
      if (this.precision !== undefined) {
        const val = this.toPrecision(value, this.precision);
        return val;
      }
      return null;
    }
  }
};
</script>
<style lang="scss" scoped>
// elementinput
::v-deep .el-input--mini .el-input__inner {
  border: 0px;
  margin: 0;
  padding: 0 15px;
  background-color: transparent;
}

.input-number-range {
  background-color: #fff;
  border-radius: 4px;
}

.flex {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  align-items: center;

  .center {
    margin-top: 1px;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.is-disabled {
  background-color: #eef0f6;
  border-color: #e4e7ed;
  color: #c0c4cc;
  cursor: not-allowed;
}
</style>
